<template>
    <div class="Assets mb-60">
        <MyMenu />
        <a-row class="t-left">
            <a-col :span="this.g.container.span" :offset="this.g.container.offset">
                <a-row type="flex" style="display:none;">
                    <a-statistic title="Status" value="Pending" />
                    <a-statistic title="Price" prefix="$" :value="568.08" :style="{margin: '0 32px',}" />
                    <a-statistic title="Balance" prefix="$" :value="3345.08" />
                </a-row>

                <a-row :gutter="16" style="display:none;">
                    <a-col :span="12">
                        <a-card>
                            <a-statistic title="Feedback" :value="11.28" :precision="2" suffix="%" :value-style="{ color: '#3f8600' }" style="margin-right: 50px">
                                <template #prefix>
                                    <a-icon type="arrow-up" />
                                </template>
                            </a-statistic>
                        </a-card>
                    </a-col>
                    <a-col :span="12">
                        <a-card>
                            <a-statistic title="Idle" :value="9.3" :precision="2" suffix="%" class="demo-class" :value-style="{ color: '#cf1322' }">
                                <template #prefix>
                                    <a-icon type="arrow-down" />
                                </template>
                            </a-statistic>
                        </a-card>
                    </a-col>
                </a-row>

                <!-- Start -->
                <a-row>
                    <a-col>
                        <!-- Total Assets -->
                        <a-card>
                            <h3>Total Assets : 123456 USD</h3>
                        </a-card>

                    </a-col>
                </a-row>

                <a-row>
                    <!-- BTC -->
                    <a-card>
                        <a-row class="t-left">
                            <h4>BTC</h4>
                        </a-row>
                        <a-row>
                            <a-col :span="12" class="t-left">
                                {{walletList.btc.amount}} BTC ≈ {{walletList.btc.amount}} USD
                            </a-col>
                            <a-col :span="12" class="t-right">
                                <a-button-group>
                                    <a-button @click="onClickShowWalletAddress('btc')" type="primary" ghost>
                                        Recharge
                                    </a-button>
                                    <a-button @click="onClickWithdraw" type="danger" ghost>
                                        Withdraw
                                    </a-button>
                                </a-button-group>
                            </a-col>
                        </a-row>

                        <a-row v-if="is_show_wallet_address=='btc'">
                            <h5>Wallet Address</h5>
                            <div id="qrcode"></div>
                            <a-row type="flex">
                                <a-col flex="auto">
                                    <a-input id="wallet_address" :value='wallet_address' placeholder="wallet address" read-only />
                                </a-col>
                                <a-col flex="60px">
                                    <a-button @click="onClickCopy()" type="primary" ghost>COPY</a-button>
                                </a-col>
                            </a-row>
                        </a-row>
                    </a-card>

                    <!-- ETH -->
                    <a-card>
                        <a-row class="t-left">
                            <h4>ETH</h4>
                        </a-row>
                        <a-row>
                            <a-col :span="12" class="t-left">
                                {{walletList.eth.amount}} ETH ≈ {{walletList.eth.amount}} USD
                            </a-col>
                            <a-col :span="12" class="t-right">
                                <a-button-group>
                                    <a-button @click="onClickShowWalletAddress('eth')" type="primary" ghost>
                                        Recharge
                                    </a-button>
                                    <a-button @click="onClickWithdraw" type="danger" ghost>
                                        Withdraw
                                    </a-button>
                                </a-button-group>

                            </a-col>
                        </a-row>

                        <a-row v-if="is_show_wallet_address=='eth'">
                            <h5>Wallet Address</h5>
                            <div id="qrcode"></div>
                            <a-row type="flex">
                                <a-col flex="auto">
                                    <a-input id="wallet_address" :value='wallet_address' placeholder="wallet address" read-only />
                                </a-col>
                                <a-col flex="60px">
                                    <a-button @click="onClickCopy()" type="primary" ghost>COPY</a-button>
                                </a-col>
                            </a-row>
                        </a-row>
                    </a-card>

                    <!-- USDT -->
                    <a-card>
                        <a-row class="t-left">
                            <h4>USDT</h4>
                        </a-row>
                        <a-row>
                            <a-col :span="12" class="t-left">
                                {{walletList.usdt.amount}} USDT ≈ {{walletList.usdt.amount}} USD
                            </a-col>
                            <a-col :span="12" class="t-right">
                                <a-button-group>
                                    <a-button @click="onClickShowWalletAddress('usdt')" type="primary" ghost>
                                        Recharge
                                    </a-button>
                                    <a-button @click="onClickWithdraw" type="danger" ghost>
                                        Withdraw
                                    </a-button>
                                </a-button-group>

                            </a-col>
                        </a-row>

                        <a-row v-if="is_show_wallet_address=='usdt'">
                            <a-radio-group v-model="usdt_type" @change="onChangeRadio">
                                <a-radio-button value="trc20">
                                    TRC-20
                                </a-radio-button>
                                <a-radio-button value="erc20">
                                    ERC-20
                                </a-radio-button>
                            </a-radio-group>
                            <h5>Wallet Address</h5>
                            <div id="qrcode"></div>
                            <a-row type="flex">
                                <a-col flex="auto">
                                    <a-input id="wallet_address" :value='wallet_address' placeholder="wallet address" read-only />
                                </a-col>
                                <a-col flex="60px">
                                    <a-button @click="onClickCopy()" type="primary" ghost>COPY</a-button>
                                </a-col>
                            </a-row>
                        </a-row>
                    </a-card>

                    <!-- 平台币 -->
                    <a-card v-for="item in platform_currency" :key="item.id">
                        <a-row class="t-left">
                            <h4>{{item.type.toLocaleUpperCase()}}</h4>
                        </a-row>
                        <a-row>
                            <a-col :span="12" class="t-left">
                                {{item.amount}} LETH ≈ {{item.amount}} USD
                            </a-col>
                            <a-col :span="12" class="t-right">
                                <a-button-group>
                                    <a-button @click="onClickWithdraw" type="danger" ghost>
                                        Withdraw
                                    </a-button>
                                </a-button-group>

                            </a-col>
                        </a-row>
                    </a-card>

                </a-row>
                <div class="mtb30">
                    <h5>Recharge Record</h5>
                </div>
                <a-table :columns="columns" :dataSource="tableDatas" :loading="loading" :pagination="pagination" @change="handleTableChange" :rowKey="tableDatas => tableDatas.id">
                    <span slot="action" slot-scope="text, record">
                        <div class="tabBtn">
                            <a-popover placement="bottomRight" overlayClassName="tableBtn">
                                <template slot="title">
                                    <a href="javascript:;" @click="handleAdd(record)">
                                        <i class="iconfont icon-table-edit" />编辑
                                    </a>
                                    <a href="javascript:;" @click="deleHostData(record)">
                                        <i class="iconfont icon-tableEmpty" />删除
                                    </a>
                                </template>
                                <span>
                                    <i class="iconfont icon-tableMore" />
                                </span>
                            </a-popover>
                        </div>
                    </span>
                </a-table>

            </a-col>
        </a-row>

    </div>
</template>


<script>
import MyMenu from "@/components/MyMenu.vue";
export default {
    name: "Assets",
    components: {
        MyMenu,
    },
    data() {
        return {
            loading: false,
            wallet_address: "0",
            is_show_wallet_address: "",
            usdt_type: "trc20",
            walletList: {
                btc: { amount: 0, address: '1' },
                eth: { amount: 0, address: '2' },
                usdt: { amount: 0, address_trc20: '3', address_erc20: '4' },
            },
            platform_currency: [],

            tableDatas: [],
            pagination: {
                total: 0,
                pageSize: 5,
            },
            queryParam: {
                page: 1,
                size: 5,
                user_id: null,
            },
            columns: [
                {
                    title: "Coin Type",
                    dataIndex: "wallet_type",
                    width: "10%",
                }, {
                    title: "Amount",
                    dataIndex: "amount",
                    width: "20%",
                }, {
                    title: "Address",
                    dataIndex: "to_address",
                }, {
                    title: "Time",
                    dataIndex: "create_datetime",
                    width: "20%",
                },
            ],
        };
    },
    methods: {
        onClickShowWalletAddress(symbol) {
            if (this.is_show_wallet_address == symbol) {
                this.is_show_wallet_address = "";
                return;
            }
            this.is_show_wallet_address = symbol;

            if (symbol == "usdt") {
                this.wallet_address = this.walletList[symbol]['address' + "_" + this.usdt_type]
            } else {
                this.wallet_address = this.walletList[symbol].address
            }
            this.updateWalletAddress()
        },
        onChangeRadio(e) {
            this.wallet_address = this.walletList.usdt['address' + "_" + this.usdt_type]
            this.updateWalletAddress()
        },
        onClickCopy() {
            $("#wallet_address").attr("readonly", "");
            $("#wallet_address").val();
            $("#wallet_address").select();
            document.execCommand("copy");
            alert("Copy Successful ：\n" + $("#wallet_address").val());
            $("#wallet_address").attr("readonly", "readonly");
        },
        onClickWithdraw() {
            this.$router.push("/my/withdraw");
        },
        handleTableChange(pagination) {
            this.pagination.current = pagination.current;
            this.pagination.pageSize = pagination.pageSize;
            this.queryParam.page = pagination.current;
            this.queryParam.size = pagination.pageSize;
            this.getTableList();
        },
        getTableList() {
            $.get(`${this.g.apiUrl}/api/my/assets/list`, this.queryParam, (res) => {
                const pagination = { ...this.pagination };
                pagination.total = res.data.page.total;
                this.tableDatas = this.formatDate(res.data.list);
                this.pagination = pagination;
            }).fail((err) => {
                this.$message.error(err);
            })
        },
        getWallet(walletList, coinName) {
            for (let i = 0; i < walletList.length; i++) {
                if (walletList[i].type == coinName) {
                    return walletList[i]
                }
            }
            return null
        },
        updateWalletAddress() {
            setTimeout(() => {
                this.initQR(this.wallet_address);
                $('#wallet_address').val(this.wallet_address)
            }, 10);
        },
        formatDate(list) {
            // let _t=this
            let a = list;
            a.forEach(function (item) {
                item.create_datetime = moment(item.time).format(
                    "YYYY-MM-DD HH:mm:ss"
                );
            });
            return a;
        },
        initQR(text) {
            $('#qrcode').html("")
            jQuery("#qrcode").qrcode({
                width: 128,
                height: 128,
                text: text,
            });
            // $("#qrcode > canvas").on("click", () => {
            //     this.downloadCanvasImamge(
            //         "canvas",
            //         "qrcode-" + this.wallet_address + ".png"
            //     );
            // });
        },
        initAssets() {
            const user_id = this.g.userInfo.id
            $.get(`${this.g.apiUrl}/api/my/assets`, { user_id }, (res) => {
                this.walletList.btc = this.getWallet(res.data.walletList, 'btc')
                this.walletList.eth = this.getWallet(res.data.walletList, 'eth')
                this.walletList.usdt = {
                    amount: this.getWallet(res.data.walletList, 'usdt').amount,
                    address_trc20: this.getWallet(res.data.walletList, 'usdt').address,
                    address_erc20: this.getWallet(res.data.walletList, 'eth').address,
                }

                let a = []
                for (let i = 0; i < res.data.walletList.length; i++) {
                    let item = res.data.walletList[i]
                    if (item.is_platform == 1) {
                        a.push(item)
                    }
                }
                this.platform_currency = a
            }).fail((err) => {
                this.$message.error(err);
            })
        },
        init() {
            if (this.g.userInfo) {
                this.queryParam.user_id = this.g.userInfo.id
                this.initAssets()
                this.getTableList()
            }
        },
    },
    mounted() {
        this.init();
    },
};
</script>



<style scoped>
</style>